import React, { useState } from "react"
import {
  DocumentsCollectionAudioItem,
  DocumentsCollectionEventInfo,
  DocumentsCollectionRPJSONItem,
} from "@rp/react/tanstack-api/documents"
import { Box } from "@rp/react/web/mui/core"
import { DocumentsCollectionBody } from "../DocumentsCollectionBody"
import { DocumentsCollectionHeader } from "../DocumentsCollectionHeader"

type DocumentsCollectionViewerProps = {
  audioItem: DocumentsCollectionAudioItem | null
  visualItems: Array<DocumentsCollectionRPJSONItem> | null
  eventInfo: DocumentsCollectionEventInfo | null
  documentId: string
  isFromChat: boolean
}

const DocumentsCollectionViewer = ({
  audioItem,
  visualItems,
  eventInfo,
  documentId,
  isFromChat,
}: DocumentsCollectionViewerProps) => {
  const [tabIdx, setTabIdx] = useState(0)

  return (
    <Box sx={{ width: "100vw" }}>
      <DocumentsCollectionHeader
        eventInfo={eventInfo}
        audioItem={audioItem}
        tabIdx={tabIdx}
        setTabIdx={setTabIdx}
        visualItems={visualItems}
        isFromChat={isFromChat}
      />

      <DocumentsCollectionBody
        visualItems={visualItems}
        documentId={documentId}
        eventInfo={eventInfo}
        tabIdx={tabIdx}
        isFromChat={isFromChat}
      />
    </Box>
  )
}

export { DocumentsCollectionViewer }
